<template>
  <el-dialog 
    :title="title" 
    :visible.sync="visible" 
    width='500px' 
    :close-on-click-modal="false" 
    :close-on-press-escape="false">
    <div v-loading = "loading">
      <el-checkbox 
        :indeterminate="isIndeterminate" 
        v-model="checkAll" 
        @change="handleCheckAllChange">全选</el-checkbox>
      <el-checkbox-group
        class="mt10" 
        v-model="checkedCities" 
        @change="handleCheckedCitiesChange">
        <el-checkbox 
          v-for="city in cities" 
          :label="city.storeGroupNo" 
          :key="city.storeGroupNo">
          {{city.storeGroupName}}
        </el-checkbox>
      </el-checkbox-group>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="visible = false" size="mini">取消</el-button>
      <el-button type="primary" size="mini" :disabled="isDisabled" @click="doSetHotSaleHandel">确定</el-button>
    </div>
  </el-dialog>
</template>
<script>
  import requestUrl from "@/api/GoodsMgt"
  export default {
    data() {
      return {
        title: '开启区域分类热销榜',
        loading: false,
        isDisabled: false,
        visible: false,
        msg: {},
        
        checkAll: false,
        checkedCities: [],
        cities: [],
        isIndeterminate: false
      }
    },
    methods: {
      setDefaultMsg(params) {
        this.visible = params.visible || false
        this.msg = params.msg || {}
        this.categoryId = params && params.msg ? params.msg.categoryId : ''
        let categoryName = params && params.msg ? params.msg.categoryName : ''
        this.title = `[${categoryName}] 开启区域分类热销榜`
        this.cities = params.cities || []
        this.getHotSaleRegion()
      },
      handleCheckAllChange(val) {
        this.checkedCities = val ? this.cities.map(item => {
          return item.storeGroupNo
        }) : [];
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        this.checkedCities = value || []
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      getHotSaleRegion() {
        this.loading = true
        this.$http.post(requestUrl.regionCategoryRankingDetail, {
          DIFFCITY: true,
          categoryId: this.categoryId
        }).then(res => {
          this.loading = false
          if(res.code === 0) {
            let checkedCities = res.data && res.data.length ? res.data.map(item => {
              return item.regionCode
            }) : []
            this.handleCheckedCitiesChange(checkedCities)
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doSetHotSaleHandel() {
        let regionList = []
        this.checkedCities.forEach(i => {
          this.cities.forEach(j => {
            if(i === j.storeGroupNo) {
              regionList.push(j)
            }
          })
        })
        this.isDisabled = true
        this.$http.post(requestUrl.openCityCategoryRanking, {
          DIFFCITY: true,
          categoryId: this.categoryId,
          regionList: regionList.map(item => {
            return {
              regionCode: item.storeGroupNo,
              regionName: item.storeGroupName
            }
          })
        }).then(res => {
          this.isDisabled = false
          if(res.code === 0) {
            this.$message.success("开启区域热销榜成功")
            this.visible = false
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.visible = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    }
  }
</script>


