<template>
  <div>
    <div class="error-t-w" v-show='isShowTip'>
      <i class="el-icon-warning"></i>注意：所有区域、所有平台共用一套分类，且变动后会立即同步三方平台，请谨慎操作！!
      <span class="c-btn-w"><i class="el-icon-close" @click="isShowTipHandel"></i></span>
    </div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddFirstLevel" size="mini" icon="el-icon-plus">添加一级分类</el-button>
        <el-button type="primary" @click="goCategoryPublishStatePage" size="mini" icon="el-icon-view">查看分类同步状态</el-button>
        <el-button type="primary" @click="doExportDataHandel" size="mini" icon="el-icon-download" :loading="exportLoading">导出</el-button>
        <el-button type="primary" @click="doSortOnceHandel" size="mini" style="float:right;" v-if="!canMove">编辑排序</el-button>
        <el-button type="primary" @click="doSaveSortHandel" :disabled="isDisabled" size="mini" style="float:right;" v-else>保存排序</el-button>
      </div>
    </div>

    <div class="l-r-2-table-wraper" v-loading="loading">
      <!-- 一级分类盒子 -->
      <div class="l-list-wraper">
        <div class="l-t-t">一级分类</div>
        <div class="l-item-th-wraper">
          <div class="l-i-t-1">分类ID</div>
          <div class="l-i-t-2">分类名称</div>
          <div class="l-i-t-2">操作</div>
          <div class="l-i-t-3" v-show="canMove"></div>
        </div>
        <!-- 可拖拽的盒子 -->
        <draggable v-model="firstLevelList" :move="getdata" @update="datadragEnd" v-if="canMove">
          <transition-group>
            <div v-for="item in firstLevelList" :key="item.categoryId" class="l-item-td-wraper" :class="{'l-i-active':item.categoryId == activeId}" @click="chooseCategory(item.categoryId)">
              <div class="l-i-t-1">{{item.categoryId}}</div>
              <div class="l-i-t-2">
                <p>{{item.categoryName}}</p>
                <p v-if="item.childCategoryNum" class="c-wrap">({{item.childCategoryNum}}个二级分类)</p>
                <p v-else-if="item.productNum" class="c-wrap">({{item.productNum}}个商品)</p>
                <p v-else class="c-wrap">(0个二级分类)</p>
              </div>
              <div class="l-i-t-2">
                <p>
                  <el-button type="text" icon="el-icon-edit" size="mini" @click="updateFirstLevel(item)">编辑</el-button>
                  <el-button type="text" icon="el-icon-delete" size="mini" @click="deleteCategory(item)" v-if="item.productNum || (item.childCategoryNum || (!item.childCategoryNum && !item.productNum))">删除</el-button>
                </p>
                <p>
                  <el-button type="text" icon="el-icon-open" size="mini" @click="openRegionHotSale(item)">开启区域热销榜</el-button>
                </p>
                <p v-if="item.childCategoryNum || (!item.childCategoryNum && !item.productNum)">
                  <el-button type="text" icon="el-icon-plus" size="mini" @click="goAddSecondLevel(item.categoryId)">添加二级分类</el-button>
                </p>
              </div>
              <div class="l-i-t-3" v-show="canMove">
                <img src="/shop/static/imgs/move.png" alt="">
                <span>按住拖拽</span>
              </div> 
            </div>
          </transition-group>
        </draggable>    
        <!-- 不可拖拽的盒子 -->
        <div v-for="item in firstLevelList" :key="item.categoryId" class="l-item-td-wraper" :class="{'l-i-active':item.categoryId == activeId}" @click="chooseCategory(item.categoryId)" v-else>
          <div class="l-i-t-1">{{item.categoryId}}</div>
          <div class="l-i-t-2">
            <p>{{item.categoryName}}</p>
            <p v-if="item.childCategoryNum" class="c-wrap">({{item.childCategoryNum}}个二级分类)</p>
            <p v-else-if="item.productNum" class="c-wrap">({{item.productNum}}个商品)</p>
            <p v-else class="c-wrap">(0个二级分类)</p>
          </div>
          <div class="l-i-t-2">
            <p>
              <el-button type="text" icon="el-icon-edit" size="mini" @click="updateFirstLevel(item)">编辑</el-button>
              <el-button type="text" icon="el-icon-delete" size="mini" @click="deleteCategory(item)" v-if="item.productNum || (item.childCategoryNum || (!item.childCategoryNum && !item.productNum))">删除</el-button>
            </p>
            <p>
              <el-button type="text" icon="el-icon-open" size="mini" @click="openRegionHotSale(item)">开启区域热销榜</el-button>
            </p>
            <p v-if="item.childCategoryNum || (!item.childCategoryNum && !item.productNum)">
              <el-button type="text" icon="el-icon-plus" size="mini" @click="goAddSecondLevel(item.categoryId)">添加二级分类</el-button>
            </p>
          </div>
          <div class="l-i-t-3" v-show="canMove">
            <img src="/shop/static/imgs/move.png" alt="">
            <span>按住拖拽</span>
          </div>
        </div>

        <div v-show="firstLevelList.length <= 0" class="no-data-wrap">暂无数据</div> 
      </div>
      <!-- 二级分类盒子 -->
      <div class="r-list-wraper">
        <div class="r-t-t">二级分类</div>
        <div class="l-item-th-wraper">
          <div class="l-i-t-1">分类ID</div>
          <div class="l-i-t-2">分类名称</div>
          <div class="l-i-t-2">操作</div>
          <div class="l-i-t-3" v-show="canMove"></div>
        </div>
        <!-- 可拖拽的盒子 -->
        <draggable v-model="secondLevelList" :move="getSLevelData" @update="sLevelDatadragEnd" v-if="canMove">
          <transition-group>
            <div v-for="item in secondLevelList" :key="item.categoryId" class="l-item-td-wraper">
              <div class="l-i-t-1">{{item.categoryId}}</div>
              <div class="l-i-t-2">
                <p>{{item.categoryName}}</p>
                <p v-if="item.productNum && item.productNum!=''" class="c-wrap">({{item.productNum}}个商品)</p>
                <p v-else class="c-wrap">(0个商品)</p>
              </div>
              <div class="l-i-t-2">
                <p>
                  <el-button type="text" icon="el-icon-edit" size="mini" @click="updateSecondLevel(item)">编辑</el-button>
                </p>
                <p>
                  <el-button type="text" icon="el-icon-delete" size="mini" @click="deleteCategory(item)">删除</el-button>
                </p>
              </div>
              <div class="l-i-t-3" v-show="canMove">
                <img src="/shop/static/imgs/move.png" alt="">
                <span>按住拖拽</span>
              </div>         
            </div>
          </transition-group>
        </draggable>
        <!-- 不可拖拽的盒子 -->
        <div v-for="item in secondLevelList" :key="item.categoryId" class="l-item-td-wraper" v-else>
          <div class="l-i-t-1">{{item.categoryId}}</div>
          <div class="l-i-t-2">
            <p>{{item.categoryName}}</p>
            <p v-if="item.productNum && item.productNum!=''" class="c-wrap">({{item.productNum}}个商品)</p>
            <p v-else class="c-wrap">(0个商品)</p>
          </div>
          <div class="l-i-t-2">
            <p>
              <el-button type="text" icon="el-icon-edit" size="mini" @click="updateSecondLevel(item)">编辑</el-button>
            </p>
            <p>
              <el-button type="text" icon="el-icon-delete" size="mini" @click="deleteCategory(item)">删除</el-button>
            </p>
          </div>
          <div class="l-i-t-3" v-show="canMove">
            <img src="/shop/static/imgs/move.png" alt="">
            <span>按住拖拽</span>
          </div>         
        </div>

        <div v-show="secondLevelList.length <= 0" class="no-data-wrap">暂无数据</div>
      </div>
    </div>
    
    <!-- 添加 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" width='500px'>
      <el-form :model="addParamMesg">
        <el-form-item label="分类名称" :label-width="formLabelWidth" required>
          <el-input v-model="addParamMesg.categoryName" autocomplete="off" placeholder="请输入分类名称" size="small" class="search-input"></el-input>
          <span>限制不超过8个字</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doAddHandelFirstLevel" :disabled="isDisabled" size="mini" v-if="isFirstLevelAdd">确 定</el-button>
        <el-button type="primary" @click="doAddHandelSecondLevel" :disabled="isDisabled" size="mini" v-else>确 定</el-button>
      </div>
    </el-dialog>
    
    <!-- 修改 -->
    <el-dialog :title="updateDialogTitle" :visible.sync="updateDialogFormVisible" width='500px'>
      <el-form :model="updateParamMesg">
        <el-form-item label="分类名称" :label-width="formLabelWidth" required>
          <el-input v-model="updateParamMesg.categoryName" autocomplete="off" placeholder="请输入分类名称" size="small" class="search-input"></el-input>
          <span>限制不超过8个字</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateDialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doUpdateHandel(true)" size="mini" v-if="isFirstLevelUpdate" :disabled="isDisabled">确 定</el-button>
        <el-button type="primary" @click="doUpdateHandel" size="mini" v-else :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 发布平台 -->
    <el-dialog title="发布" :visible.sync="publishDialogFormVisible" width='500px'>
      <el-form :model="publishParamMesg">
        <el-form-item label="发布平台" :label-width="formLabelWidth" required>
          <el-checkbox-group v-model="publishParamMesg.checkedPlatform">
            <el-checkbox v-for="item in platformList" :label="item.platformId" :key="item.platformId">{{item.name}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="publishDialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doPublishPlatformHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>

    <hot-sale ref="setRegionHotSale"></hot-sale>
  </div>
</template>

<script>
import commonUrl from "@/api/Common"
import Draggable from 'vuedraggable'
import Storage from '@/utils/localStorage'
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportExcel'
import SetHotSale from "./SetHotSale"
const categoryMgtUrl = {
  // 获取商家平台
  getPlatformList: '/plateform/a/product/getPlatformList',
  // 添加数据
  insertCategory: '/plateform/a/category/insertCategory',
  // 获取分类内容
  getCategoryList: '/plateform/a/category/getCategoryList',
  // 分类管理-分类排序
  sortCategory: '/plateform/a/category/sortCategory',
  // 分类管理-修改分类
  updateCategory: '/plateform/a/category/updateCategory',
  // 分类发布
  pushCategory: '/plateform/a/category/pushCategory',
  // 删除分类
  delCategory: '/plateform/a/category/delCategory',
  // 导出分类
  exportCategory: '/plateform/a/category/exportCategory'
}
export default {
  data() {
    return {
      exportLoading: false,
      isShowTip: true, // 是否显示操作提示，默认显示
      loading: false,
      isDisabled: false, // 按钮是否可点击
      activeId: -1, // 选中id
      canMove: false, // 是否可以拖拽移动
      firstLevelList: [], // 一级分类列表
      secondLevelList: [],// 二级分类列表
      dialogFormVisible: false, // dialog显示与隐藏
      updateDialogFormVisible: false, // 修改dialog显示与隐藏
      publishDialogFormVisible: false, // 发布平台弹框显示与隐藏
      isFirstLevelAdd: true, // 是否是添加一级菜单
      isFirstLevelUpdate: true, // 是否是修改一级菜单
      formLabelWidth: '80',
      dialogTitle: '添加一级分类',
      updateDialogTitle: '修改一级分类',
      // 添加信息
      addParamMesg: {
        parentId: '',
        categoryName: ''
      },
      // 修改信息
      updateParamMesg: {
        parentId: '',
        categoryId: '', // 要修改的分类ID
        level: '', // 分类排序
        categoryName: '' // 一级分类名称，如果修改的是一级分类，该字段不能为空
      },
      platformList: [], // 平台列表
      // 选择发布平台信息
      publishParamMesg: {
        checkedPlatform: []
      },
      regionOptions: [] // 区域信息
    }
  },
  created() {
    // 获取平台
    this.getPlatformList()
    // 查询
    this.doSearchHandel()
    // 获取区域信息
    this.getUserAreaList()
  },
  components: {
    'draggable': Draggable,
    'hot-sale': SetHotSale
  },
  methods: {
    /**
     * [getPlatformList 获取平台列表]
     * @return {[type]} [description]
     */
    getPlatformList() {
      this.$http.get(categoryMgtUrl.getPlatformList).then(res => {
        if(res.code === 0) {
          if(res.data && res.data.length) {
            this.platformList = res.data
          }else {
            this.platformList = []
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取平台列表):", e.message)
      })
    },
    /**
     * [goAddFirstLevel 添加一级分类]
     * @return {[type]} [description]
     */
    goAddFirstLevel() {
      this.dialogTitle = '添加一级分类'
      this.dialogFormVisible = true
      this.isFirstLevelAdd = true
      // 添加内容恢复默认
      this.addParamMesg.parentId = ''
      this.addParamMesg.categoryName = ''
    },
    /**
     * [goAddFirstLevel 添加二级分类]
     * @return {[type]} [description]
     */
    goAddSecondLevel(_val) {
      this.dialogTitle = '添加二级分类'
      this.dialogFormVisible = true
      this.isFirstLevelAdd = false
      // 添加内容恢复默认
      this.addParamMesg.parentId = _val
      this.addParamMesg.categoryName = ''
      
    },
    /**
     * [updateFirstLevel 更新一级分类]
     * @return {[type]} [description]
     */
    updateFirstLevel(_val) {
      this.updateDialogFormVisible = true 
      this.isFirstLevelUpdate = true
      this.updateDialogTitle = '编辑一级分类'

      this.updateParamMesg.categoryId = _val.categoryId
      this.updateParamMesg.categoryName = _val.categoryName
      this.updateParamMesg.level = _val.level
    },
    /**
     * [updateSecondLevel 更新二级分类]
     * @return {[type]} [description]
     */
    updateSecondLevel(_val) {
      this.updateDialogFormVisible = true 
      this.isFirstLevelUpdate = false
      this.updateDialogTitle = '编辑二级分类'

      this.updateParamMesg.categoryId = _val.categoryId
      this.updateParamMesg.categoryName = _val.categoryName
      this.updateParamMesg.level = _val.level
      this.updateParamMesg.parentId = _val.parentId
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel() {
      this.loading = true
      this.$http.post(categoryMgtUrl.getCategoryList).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.length) {
            this.firstLevelList = res.data.sort(utils.compareKey('level'))
            if(this.activeId != -1) {
              this.chooseCategory(this.activeId)
            }
          }else {
            this.firstLevelList = []
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [showDetail 显示详情]
     * @param  {[type]} _param [id]
     * @return {[type]}        [description]
     */
    showDetail(_row) {
      this.$router.push('/categories_mgt/edit/' + _row.categoryId)
    },
    /**
     * [getdata 获取数据]
     * @return {[type]} [description]
     */
    getdata(evt) {

    },
    /**
     * [datadragEnd 拖拽结束]
     * @return {[type]} [description]
     */
    datadragEnd(evt) {
      // console.log("evt结束:", evt)
      for(let [index, elem] of new Map(this.firstLevelList.map((item, i) => [i, item]))){
        elem.level = index
      }
    },
    /**
     * [getdata 获取数据]
     * @return {[type]} [description]
     */
    getSLevelData(evt) {
      // console.log(evt.draggedContext.element.categoryId)
    },
    /**
     * [datadragEnd 拖拽结束]
     * @return {[type]} [description]
     */
    sLevelDatadragEnd(evt) {
      for(let [index, elem] of new Map(this.secondLevelList.map((item, i) => [i, item]))){
        elem.level = index
      }
    },
    /**
     * [chooseCategory 分类变化]
     * @return {[type]} [description]
     */
    chooseCategory(_val) {
      this.activeId = _val
      this.$http.post(categoryMgtUrl.getCategoryList, {parentId: this.activeId}).then(res => {
        if(res.code === 0) {
          if(res.data && res.data.length) {
            this.secondLevelList = res.data.sort(utils.compareKey('level'))
          }else {
            this.secondLevelList = []
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$notify.error({
          title: '提示',
          message: e.message
        })
        console.log("提示(分类变化获取二级分类):", e.message)
      })
    },
    /**
     * [doAddHandel 确认添加一级分类]
     * @return {[type]} [description]
     */
    doAddHandelFirstLevel() {
      if(this.isDisabled === true) {
        return
      }

      let rStr = this.addParamMesg.categoryName.replace(/\s+/g, '')
      this.addParamMesg.categoryName = rStr

      if(this.addParamMesg.categoryName == "") {
        this.$message("请输入分类名称")
        return
      }else {
        if(this.addParamMesg.categoryName.length > 8) {
          this.$message("分类名称限制不超过8个字")
          return
        }
      } 
      this.isDisabled = true
      this.$http.post(categoryMgtUrl.insertCategory, {categoryName: this.addParamMesg.categoryName, categoryLevel: 1}).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("添加一级分类成功")
          this.dialogFormVisible = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })      
    },
    /**
     * [doAddHandel 确认添加二级分类]
     * @return {[type]} [description]
     */
    doAddHandelSecondLevel() {
      if(this.isDisabled === true) {
        return
      }
      let rStr = this.addParamMesg.categoryName.replace(/\s+/g, '')
      this.addParamMesg.categoryName = rStr
      if(this.addParamMesg.categoryName == "") {
        this.$message("请输入分类名称")
        return
      }else {
        if(this.addParamMesg.categoryName.length > 8) {
          this.$message("分类名称限制不超过8个字")
          return
        }
      } 
      let firstCategoryName = ''
      for(let item of this.firstLevelList) {
        if(item.categoryId == this.addParamMesg.parentId) {
          firstCategoryName = item.categoryName
        }
      }
      // 保存按钮不可再次点击
      this.isDisabled = true
      this.$http.post(categoryMgtUrl.insertCategory, {secondaryCategoryName: this.addParamMesg.categoryName, categoryLevel: 2, parentId: this.addParamMesg.parentId, categoryName: firstCategoryName}).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("添加二级分类成功")
          this.dialogFormVisible = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [doUpdateHandel description]
     * @param  {[type]} _param [true 修改一级分类（分类名称字段为categoryName） false 修改二级分类（分类名称字段为secondaryCategoryName） ]
     * @return {[type]}        [description]
     */
    doUpdateHandel(_param) {
      if(this.isDisabled === true) {
        return
      }
      let rStr = this.updateParamMesg.categoryName.replace(/\s+/g, '')
      this.updateParamMesg.categoryName = rStr
      if(this.updateParamMesg.categoryName == "") {
        this.$message("请输入分类名称")
        return
      }else {
        if(this.updateParamMesg.categoryName.length > 8) {
          this.$message("分类名称限制不超过8个字")
          return
        }
      } 

      let paramObj = {}
      if(_param === true) {
        paramObj.categoryId = this.updateParamMesg.categoryId
        paramObj.categoryName = this.updateParamMesg.categoryName
        paramObj.level = this.updateParamMesg.level
        paramObj.categoryLevel = 1
      }else {
        let firstCategoryName = ''
        for(let item of this.firstLevelList) {
          if(item.categoryId == this.updateParamMesg.parentId) {
            firstCategoryName = item.categoryName
          }
        }
        paramObj.categoryId = this.updateParamMesg.categoryId
        paramObj.categoryName = firstCategoryName
        paramObj.secondaryCategoryName = this.updateParamMesg.categoryName
        paramObj.level = this.updateParamMesg.level
        paramObj.categoryLevel = 2
      }
      // 保存按钮不可再次点击
      this.isDisabled = true
      this.$http.post(categoryMgtUrl.updateCategory, paramObj).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          if(_param === true) {
            this.$message.success("编辑一级分类成功")
          }else {
            this.$message.success("编辑二级分类成功")
          }
          
          this.updateDialogFormVisible = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })  
    },
    /**
     * [doSortOnceHandel 点击重新排序按钮触发事件]
     * @return {[type]} [description]
     */
    doSortOnceHandel() {
      this.canMove = true
    },
    /**
     * [doSaveSortHandel 点击保存排序按钮触发事件]
     * @return {[type]} [description]
     */
    doSaveSortHandel() {
      if(this.isDisabled === true) {
        return
      }
      let categoryList = []
      // 结合一级分类和二级分类
      let allCategoryList = [...this.firstLevelList, ...this.secondLevelList]
      // 组装整洁参数
      for(let item of allCategoryList) {
        let itemObj = {
          categoryId: item.categoryId,
          categoryName: item.categoryName,
          level: item.level
        }
        categoryList.push(itemObj)
      }
      // 保存按钮不可再次点击
      this.isDisabled = true
      utils.showLoading()
      this.$http.post(categoryMgtUrl.sortCategory, {categoryReqList: categoryList}).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("重新排序成功")
          this.canMove = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })

    },
    /**
     * [deleteCategory 删除一级分类]
     * @param  {[type]} _row [description]
     * @return {[type]}      [description]
     */
    deleteCategory(_row) {
      let _categoryId = _row.categoryId
      let _categoryName = _row.categoryName
      this.$confirm('确定要删除该分类吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.post(categoryMgtUrl.delCategory, {categoryId: _categoryId, categoryName: _categoryName}).then(res => {
          if(res.code === 0) {
            this.$message.success("删除分类成功！")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      }).catch(() => {})
    },
    /**
     * [doPublishPlatformHandel 发布事件]
     * @return {[type]} [description]
     */
    doPublishPlatformHandel() {
      if(this.isDisabled === true) {
        return
      }
      let platformId = this.publishParamMesg.checkedPlatform.join(',')
      // 保存按钮不可再次点击
      this.isDisabled = true
      this.$http.post(categoryMgtUrl.pushCategory, {platformId: platformId}).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("发布成功")
          this.publishDialogFormVisible = false
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [isShowTipHandel 是否显示操作提示事件]
     * @return {Boolean} [description]
     */
    isShowTipHandel() {
      this.isShowTip = false
    },
    /**
     * [goCategoryPublishStatePage 去查看分类同步状态页面]
     * @return {[type]} [description]
     */
    goCategoryPublishStatePage() {
      this.$router.push("/categories_mgt/pub_state")
    },
    /**
     * @description: 导出分类
     * @param {type} 
     * @return: 
     */
    doExportDataHandel() {
      this.exportLoading = true
      this.$http.post(categoryMgtUrl.exportCategory).then(res => {
        if(res.code === 0) {
          this.exportLoading = false
          let exportData = res.data && res.data.length ? res.data : []
          let tempConfig = {
            categoryId: '一级分类id',
            categoryName: '一级分类名称',
            secondaryCategoryId: '二级分类id',
            secondaryCategoryName: '二级分类名称'
          }
          const tHeader = utils.objectToArray(tempConfig, false)
          const filterVal = utils.objectToArray(tempConfig, true)
          exportSheet.ExportJsonToExcel({
            header: tHeader,
            filterVal,
            data: exportData,
            filename: `分类`
          })
          this.$message.success('分类导出成功')
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.exportLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * @description: 开启区域热销榜
     */
    openRegionHotSale(row) {
      let params = {
        msg: row,
        visible: true,
        cities: this.regionOptions
      }
      this.$refs.setRegionHotSale.setDefaultMsg(params)
    },
    /**
     * @description: 获取区域信息
     */
    getUserAreaList() {
      this.$http.get(commonUrl.userAreaStoreGroupList).then(res => {
        if(res.code === 0) {
          this.regionOptions = res.data && res.data.length ? res.data : []
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("【提示】查询用户区域店组一级列表失败：" + e.message)
      })
    }

  }
}
</script>


